import React,  {Fragment,useState,useEffect}  from 'react'
import {Link, Redirect} from 'react-router-dom'
import {connect} from 'react-redux'
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';

import MenuItem from '@mui/material/MenuItem';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import { DataGrid } from '@mui/x-data-grid';
import {get_users,get_category,get_inventory,get_zones,create_stock} from '../../actions/manager'
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import Stack from '@mui/material/Stack';

import Pagination from '@mui/material/Pagination';

import moment from 'moment'

const Screen  = ({auth,get_users,get_inventory,get_zones,create_stock}) => {
    const [users,setUsers] = useState( [])
    const [selectUser,setSelectUser] = useState(null)
    const [inventory,setInventory] = useState(null)

    const [zones,setZones] = useState( [])
    const [page,setPage] = useState(1)

    const [total,setTotal] = useState(10)
    const limit = 10;

    useEffect(async () => {

        const res = await get_users();
        console.log(res);
        setUsers( res.data)
        const res2 = await get_zones();
        setZones( res2.data)
        const res3 = await get_inventory({is_in_stock:false})
        setInventory(res3.data)
        setPage(res3.data.page)
        setTotal(res3.data.total)
      },[]);
  
    const handleChangeUser = async (event) => {
        setSelectUser(event.target.value);

        const res3 = await get_inventory({user:event.target.value})
        setInventory(res3.data)
    };

    return (
        <div style={{ height: 400, width: '100%' }}>
            <h1>Dashboard</h1>
            <Box
            component="form"
            sx={{
                '& > :not(style)': { m: 1, width: '25ch' },
            }}
            noValidate
            autoComplete="off"
            >
              
               {selectUser && <TextField
                    id="outlined-select-currency"
                    select
                    label="user"
                    value={selectUser}
                    onChange={handleChangeUser}
                    helperText="Please select users"
                    size="small"
                    disabled = {false}
                    >
                {users.map((option) => (
                    <MenuItem key={option._id} value={option._id}>
                    {option.name}
                    </MenuItem>
                ))}
                </TextField>} 
             
               <br/>

            </Box>
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 650 }} aria-label="simple table"style={{borderCollapse:'collapse'}}>
                    <TableHead>
                    <TableRow>
                        <TableCell>inventory name</TableCell>
                        <TableCell align="right">category</TableCell>
                        <TableCell align="right">amount</TableCell>
                        <TableCell align="right">create date</TableCell>
                        <TableCell align="right">import</TableCell>
                    </TableRow>
                    </TableHead>
                    <TableBody>
                    {inventory && inventory.list.map((row) => (
                        <TableRow
                        key={row._id}
                        sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                        >
                        <TableCell component="th" scope="row">
                            {row.name}
                        </TableCell>

                        <TableCell align="right">{row.category.name}</TableCell>
                        <TableCell align="right">{row.amount}</TableCell>
                        <TableCell align="right">{moment(row.create_date).format('LL')}</TableCell>

                        <TableCell align="right">
                            <form onSubmit = {async (event)=>{
                                event.preventDefault();
                                const form = event.target;
                                // get the field that you want
                                const userInputField = form.elements['select']
                                const res = await create_stock({inventory:row._id,zone:userInputField.value})
                                console.log(res);
                                const res3 = await get_inventory({is_in_stock:false})
                                setInventory(res3.data)
                            }}> 
                                <Stack
                                    direction={{ xs: 'column', sm: 'row' }}
                                    spacing={{ xs: 1, sm: 2, md: 4 }}
                                >
                                <select style={{width:100,justifyContent:'center',paddingLeft:20}} name="select">
                                    {zones && zones.map(item => {
                                       return  <option key={item._id} value={item._id}>{item.name}</option>
                                    })}
                                </select>
                                <button type='submit'>save</button>
                                </Stack>
                            </form>
                        </TableCell>

                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
            </TableContainer>
            <Stack spacing={2}>
                {total/limit > 1 &&
                    <Pagination count={Math.ceil(total/limit)} page={page} size="small" onChange = {async (event, value)=>{
                            setPage(value);
                            var query ={limit:limit,page:value,is_in_stock:false}
                            if(selectUser) query.user = selectUser
                           
                            const res3 = await get_inventory(query)
                            setInventory(res3.data)
                            setTotal(res3.data.total)
                    }}/>
                }
                
            </Stack>
        </div>
    )
}


const mapStateToProps = state => ({
    auth:state.auth,

})
export default connect(mapStateToProps,{get_users,get_inventory,create_stock,get_zones})(Screen)
