import React,  {Fragment,useState,useEffect}  from 'react'
import {Link, Redirect} from 'react-router-dom'
import {connect} from 'react-redux'
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';

import MenuItem from '@mui/material/MenuItem';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import { DataGrid } from '@mui/x-data-grid';
import {get_users,list_history} from '../../actions/manager'
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

import Stack from '@mui/material/Stack';

import Pagination from '@mui/material/Pagination';
import moment from 'moment'

const Screen  = ({auth,get_users,list_history}) => {
    const [users,setUsers] = useState( [])
    const [selectUser,setSelectUser] = useState(null)
    const [stocks,setStocks] = useState(null)

    const [page,setPage] = useState(1)

    const [total,setTotal] = useState(10)
    const limit = 10;
    useEffect(async () => {

        const res = await get_users();
        console.log(res);
        if(res.success){
            setUsers( res.data)
            const res3 = await list_history({limit:limit,page:page})
            setStocks(res3.data)
            setPage(res3.data.page)
            setTotal(res3.data.total)
        }
   
      },[]);
  
    const handleChangeUser = async (event) => {
        setSelectUser(event.target.value);

        const res3 = await list_history({user:event.target.value})
        if(res3.success){
            setStocks(res3.data)
            setPage(res3.data.page)
            setTotal(res3.data.total)
        }
     
    };

    return (
        <div style={{ height: 400, width: '100%',marginBottom:50 }}>
            <h1>History</h1>
            <Box
            component="form"
            sx={{
                '& > :not(style)': { m: 1, width: '25ch' },
            }}
            noValidate
            autoComplete="off"
            >
              
                <TextField
                    id="outlined-select-currency"
                    select
                    label="user"
                    value={selectUser}
                    onChange={handleChangeUser}
                    helperText="Please select users"
                    size="small"
                    disabled = {false}
                    >
                {users && users.map((option) => (
                    <MenuItem key={option._id} value={option._id}>
                    {option.name}
                    </MenuItem>
                ))}
                </TextField>
        

            </Box>
            <TableContainer component={Paper} style={{marginBottom:20}}>
                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                    <TableHead>
                    <TableRow>
                        <TableCell>inventory name</TableCell>
                        <TableCell align="right">category</TableCell>
                        <TableCell align="right">amount</TableCell>
                        <TableCell align="right">create date</TableCell>
                    </TableRow>
                    </TableHead>
                    <TableBody>
                    {stocks && stocks.list.map((row) => (
                        <TableRow
                        key={row._id}
                        sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                        >
                        <TableCell component="th" scope="row">
                            {row.inventory.name}
                        </TableCell>

                        <TableCell align="right">{row.zone.name}</TableCell>
                        <TableCell align="right">{row.current_amount}</TableCell>
                        <TableCell align="right">{moment(row.create_date).format('LL')}</TableCell>

                        </TableRow>
                    ))}
                    </TableBody>
                </Table>
            </TableContainer>
            <Stack spacing={2}>
                {total/limit > 1 &&
                    <Pagination count={Math.ceil(total/limit)} page={page} size="small" onChange = {async (event, value)=>{
                            setPage(value);
                            var query ={limit:limit,page:value}
                            if(selectUser) query.user = selectUser
                            const res3 = await list_history(query)
                            setStocks(res3.data)
                            setTotal(res3.data.total)
                    }}/>
                }
                
            </Stack>
        </div>
    )
}


const mapStateToProps = state => ({
    auth:state.auth,

})
export default connect(mapStateToProps,{get_users,list_history})(Screen)
